.App {
  text-align: center;
}

#page-body {
}

nav ul {
  display: flex;
  justify-content: center;
  background-color: #a2d2df;
  padding: 1rem;
  margin: 0;
}

nav ul li {
  list-style: none;
  padding: 1rem;
}

nav ul li a {
  text-decoration: none;
  color: black;
}

.articles-in-list,
.about-text {
  /* display: flex;
  justify-content: center;
  flex-direction: column; */
  /* text-align: left; */
  padding: 1rem;
  max-width: 800px;
}

.articles-in-list h3,
p,
a {
  text-decoration: none;
  color: black;
}

button {
  background-color: #a2d2df;
  border-radius: 8px;
  padding: 0.5rem;
}

.article-page {
  padding: 1rem;
}

form {
  padding: 1rem;
  max-width: 800px;
}

form label {
  display: flex;
  flex-direction: column;
  text-align: left;
  padding: 1rem;
}

nav a:hover {
  color: gray;
}

.comment {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
  padding: 0rem 1rem;
  margin: 1rem;
  border: 1px solid rgb(228, 227, 227);
  border-radius: 8px;
  max-width: 800px;
}
